body,
.tooltip,
.popover,
.btn,
.m-portlet.m-portlet--creative .m-portlet__head .m-portlet__head-caption .m-portlet__head-title .m-portlet__head-text,
.vakata-context,
.vakata-context ul
{
  font-family: Roboto;
}
.m-logo img{
  display: inline-block;
  width: 100px;
}
.m-logo{
  text-align: center;
  padding: 40px;
  padding-bottom: 10px;
}
.m-width-100{
  width: 100% !important;
}
.m-text-center{
  text-align: center
}

.m-menu__link-text{
  text-transform: uppercase !important;
  font-size: 36px;
}

.btn{
  padding: 0.65rem 3rem;
}
.input-group-btn, .btn-circle{
  padding: 0.65rem 1rem;
}

.m-lh-37{
  line-height: 37px;
}
.m-draggable__x.m-widget2__checkbox{
  padding-right: 0.5rem;
  padding-left: 2.0rem !important;
}
.m-widget2__settings{
  display: none;
  padding-top: 1rem;
  padding-right: 0.5rem;
  padding-left: 0.5rem !important;
}

.m-used-components .m-widget2__settings{
  display: table-cell;
}

.m-used-components .ui-sortable-helper .m-widget2__settings{
  display: none;
}

.m-form-settings{
  padding: 10px;
  margin-left: 30px;
  margin-top: 5px;
}

.m-used-components .m-accordion__item-body.collapse.show{
  padding-bottom: 1px;
}

.m-portlet__head-text{
  font-size: 18px;
}

.m--wait-overlay{
  z-index: 1010;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(255,255,255,0.75);
  height: 100%;
  width: 100%;
  display: none;
}
.m--wait{
  z-index: 1011;
  position: fixed;
  padding: 0;
  margin: 0;
  width: 256px;
  top: 50%;
  left: 50%;
  text-align:center;
  color: rgb(0, 0, 0);
  border: 0;
  cursor: wait;
  display: none;
}
.m--wait-metal{
  background-color: #c4c5d6;
}
.categoryzation .col-md-4:nth-child(4){
  display: none;
}
#m_sortable .m-widget2{
  min-height: 100px;
}
.carousel-control-prev:hover, .carousel-control-prev:focus, .carousel-control-next:hover, .carousel-control-next:focus{
  opacity: 1;
}

.carousel-item{
  padding: 0 15%;
}

.carousel-control-prev{
  background: linear-gradient(to right, #f7f8fa, #f7f8fa, rgba(0,0,0,0));
}

.carousel-control-next{
  background: linear-gradient(to left, #f7f8fa, #f7f8fa, rgba(0,0,0,0));
}

.input-group {
  flex-wrap: nowrap;
}
.m-accordion__item-body.collapse,
.m-accordion__item-body.collapsed{
  display: block;
  height: 0 !important;
  overflow: hidden;
}
.m-accordion__item-body.collapse.show,
.m-accordion__item-body.collapsed.show{
  height: auto !important;
}

.m-demo__preview{
  background: white;
  border: 4px solid #5867dd;
  background: #f7f7fa;
  padding: 30px;
  margin-bottom: 20px;
}

.m-demo .m-demo__preview {
  margin-bottom: 0;
}

.tab-content-white{
  padding: 20px 20px 12px 20px;
  background: #fff;
  margin-top: -20px;
  border: 1px solid #dee2e6;
  border-top: none;
}